*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  margin: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

@media only screen and (max-width: 992px) {
  html {
    font-size: 56.25%;
  }
}
html,
body {
  height: 100%;
}

body {
  color: #221f1f;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.5;
  background-image: linear-gradient(to right bottom, #610972, #c09dc7);
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  font: inherit;
}

[role=button] {
  cursor: pointer;
}

.heading-secondary, .heading-primary {
  line-height: 1;
}

.heading-primary {
  font-size: 4rem;
  color: #610972;
  animation: moveLeft 1s ease-out;
}

@media only screen and (max-width: 992px) {
  .heading-primary {
    font-size: 3.6rem;
  }
}
.heading-secondary {
  font-size: 3rem;
  color: #221f1f;
  margin: 2rem 0;
  animation: moveRight 1s ease-out;
}

@media only screen and (max-width: 992px) {
  .heading-secondary {
    font-size: 2.4rem;
  }
}
@keyframes moveLeft {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }
  80% {
    transform: translateX(10px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveRight {
  0% {
    opacity: 0;
    transform: translateX(100px);
  }
  80% {
    transform: translateX(-10px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
.btn--check, .btn--again {
  padding: 1rem 2rem;
}

.btn {
  display: inline-block;
  background-color: #221f1f;
  border-radius: 1rem;
  color: inherit;
  font-size: 2.2rem;
  font-weight: 700;
  font-family: inherit;
  transition: all 0.4s ease-in-out;
  position: relative;
}
.btn::after {
  content: attr(data-content);
  background-color: #fff;
  color: inherit;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border: 5px dashed currentColor;
  transform: translate(-3px, -3px);
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}
.btn:hover::after {
  transform: translate(0);
}
.btn--animation {
  animation: moveBottom 1s backwards 0.7s;
}
.btn--again {
  transition: all 0.4s ease-in;
}
.container {
  min-height: 100%;
  display: grid;
  grid-template-columns: repeat(1, 94%);
  place-content: center;
}

@media only screen and (max-width: 768px) {
  .container {
    display: grid;
    grid-template-columns: repeat(1, 100%);
    place-content: unset;
  }
}
.game, .description {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 2rem;
}

@media only screen and (max-width: 768px) {
  .game, .description {
    padding: 2rem 0;
  }
}
.main {
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

@media only screen and (max-width: 768px) {
  .main {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
    border-radius: unset;
  }
}
.description {
  background-color: #dbd6d6;
}
.description__info {
  background-color: #eee;
  border-radius: 1rem;
  font-size: 2rem;
  padding: 1rem 2rem;
  margin: 0 3rem 4rem;
  animation: moveBottom 1s 0.8s backwards;
}
@media only screen and (max-width: 768px) {
  .description__info {
    font-size: 1.8rem;
    margin: 0 3rem 3rem;
  }
}

.game {
  background-color: #cbcfcb;
}
.game__number {
  background-color: #eceeec;
  border: 5px dashed currentColor;
  font-size: 6.4rem;
  font-weight: 700;
  width: 15rem;
  padding: 1rem 0;
  text-align: center;
  margin-bottom: 4rem;
}
.game__msg {
  font-size: 2.8rem;
  font-weight: 700;
  margin-bottom: 4rem;
  color: #610972;
}
@media only screen and (max-width: 768px) {
  .game__msg {
    font-size: 2rem;
  }
}
.game__checkbox {
  margin-bottom: 4.8rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 2rem;
}
.game__checkbox-input {
  border: none;
  border-radius: 1rem;
  font-size: 2.8rem;
  outline: none;
  width: 12rem;
  height: 8rem;
  text-align: center;
  padding: 1rem;
}
.game__score {
  font-size: 2.4rem;
  font-weight: 700;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 3.6rem;
}
@media only screen and (max-width: 768px) {
  .game__score {
    padding-bottom: 1.2rem;
  }
}

.score,
.highscore {
  color: #610972;
}

.footer {
  background-color: #fff;
  font-size: 1.8rem;
  margin-top: -1.6rem;
  padding: 2.4rem 4.8rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 768px) {
  .footer {
    padding: 1.6rem;
  }
}
.footer__description span {
  color: #610972;
  font-weight: 700;
}
.footer__social {
  list-style: none;
  padding: 0;
}
.footer__social li {
  transition: transform 0.4s;
}
.footer__social li:hover {
  transform: scale(1.2);
}